<template>
    <yd-layout class="box_xunjian">
        <navBar title="巡检记录" slot="navbar"></navBar>
        <div v-if="!didLoadData">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_loading.png" class="Rotation" >
                    <div>稍等哟~正在加载</div>
                </div>
            </div>
        </div>

        <div v-if="didLoadData && data.length == 0">
            <div class="boxload">
                <div class="boxload_content">
                    <img src="../../assets/icon_lose.png" >
                    <div>哎呀!还没有数据</div>
                </div>
            </div>
        </div>
        <div v-if="didLoadData && data.length > 0">
            <yd-pullrefresh :callback="doRefreshData" ref="listViewRefresh">
                <yd-infinitescroll :callback="doLoadMoreData" ref="listViewLoadMore">
                    <yd-list slot="list">
                        <div class="div_list" v-for="item,index in data" :key="index">
                            <div class="list_top">
                                <img src="../../assets/shu.png" alt="" class="shu">
                                <p class="list_title">{{item.xjdmc}}</p>
                            </div>
                            <div>{{item.xjsj}}</div>
                            <div class="list_cont">
                                <div class="list_cont_result posi">
                                    <img v-if="item.result == 1" src="../../assets/alert.png" alt="" style="width: .5rem;position: absolute;right: 0.05rem;top: 0.05rem;">
                                    <div v-if="item.result == 1" style="color: red">有隐患</div>
                                    <div v-if="item.result == 2" style="color: #909090;">未巡检</div>
                                    <div v-if="item.result == 0" style="color: #1D91E6;">无隐患</div>
                                    <p>结果</p>
                                </div>
                                <div class="list_cont_result">
                                    <div>{{item.xjbc}}</div>
                                    <p>班次</p>
                                </div>
                                <div class="list_cont_result" style="margin-right: 0">
                                    <div>{{item.xjrxm}}</div>
                                    <p>检查人</p>
                                </div>
                            </div>
                            <div class="list_org">
                                <p class="org">公司</p>
                                <p style="flex: 1;">{{item.qy_mc}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">备注</p>
                                <p style="flex: 1;">{{item.xjbz}}</p>
                            </div>
                            <div class="list_org">
                                <p class="org">附件</p>
                                <div class="flex_image">
                                    <div class="flex_image_img">
                                        <img :src="item.xjtp" alt="" style="width: 100%;height: 100%;" @click="onPressLightbox($event,item.xjtp)">
                                    </div>
                                </div>
                            </div>
                        </div>
                    </yd-list>
                </yd-infinitescroll>
            </yd-pullrefresh>
        </div>
    </yd-layout>
</template>

<script>
    import {api_xunjian_list} from '../../components/APIKeys'
    export default {
        name: "CheckList",
        data(){
            return{
                // token:window.localStorage.getItem('token'),
                xunjiandianId:window.localStorage.getItem('xunjiandianId'),
                data:[],//获取到的数据
                result:null,//巡检结果
                hasMore: true,//是否有下一页
                page: 1,//页数
                pageSize: 10,//每页个数
                didLoadData:false, //是否加载完数据
            }
        },
        activated: function () {
            if (!!this.scrollOffset && this.scrollOffset > 0) {
                this.scrollView.scrollTop = this.scrollOffset;
            }
        },
        mounted(){
            // this.doLoadData();
            this.doLoadData((res) => {
                this.data = [...res.data.data];
            });
        },
        methods:{
            //获取数据
            doLoadData(data){
                // this.$showDialog();
                let jsonData = {
                    // token: this.token,
                    pageIndex: this.page,
                    pageSize: this.pageSize,
                };
                this.$post(api_xunjian_list, jsonData).then(res =>{
                    // this.$showDialog(false);
                    this.didLoadData = true;
                    if(res.errno == 0){
                        data && data(res);
                        // this.data = res.data.data || [];
                        this.hasMore = res.data.hasMore;
                        for( let i = 0; i < this.data.length;i++){
                            if(this.data[i].xjjg == '有隐患'){
                                this.data[i].result = 1;
                            }else if(this.data[i].xjjg == '无隐患'){
                                this.data[i].result = 0;
                            }else{
                                this.data[i].result = 2;
                            }
                        }
                    }else{
                        this.$showError(res.errmsg);
                    }
                })
            },
            //点击查看大图
            onPressLightbox(e,img) {
                let photoes = [
                    {url:img,}
                ];
                this.$showLightbox(photoes);
                // fancyBox(e.target, images);
            },
            /**
             * 上拉加载
             */
            doLoadMoreData: function () {
                // console.log('this.hasMore========= ',this.hasMore);
                if (!this.hasMore) {
                    this.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                    return;
                }
                this.page++;
                let self = this;
                this.doLoadData((res) => {
                    self.data = [...self.data, ...res.data.data];
                    // self.hasMore = self.data.length < parseInt(res.total);
                    self.hasMore = res.data.hasMore;

                    if (self.data.length > 0) {
                        // 所有数据加载完成
                        if (!self.hasMore) {
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.loadedDone');
                        }
                        else {
                            /* 单次请求数据完毕 */
                            self.$refs.listViewLoadMore.$emit('ydui.infinitescroll.finishLoad');
                        }
                    }
                })
            },
            /**
             * 下拉刷新
             */
            doRefreshData: function () {
                this.page = 1;
                let self = this;
                this.doLoadData((res) => {
                    self.data = [...res.data.data];
                    if (self.$refs.listViewRefresh) {
                        self.$refs.listViewRefresh.$emit('ydui.pullrefresh.finishLoad');
                    }
                });
            },
        }
    }
</script>

<style scoped>
    .boxload {
        height: 3.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .boxload_content{
        width: 3rem;
        height:2.7rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .boxload_content img{
        width: 1.54rem;
        height: 1.54rem;
    }
    .boxload_content div{
        color: #cdcdcd;
        margin-top: 0.2rem;
    }
    @-webkit-keyframes rotation{
        from {-webkit-transform: rotate(0deg);}
        to {-webkit-transform: rotate(360deg);}
    }

    .Rotation{
        transform: rotate(360deg);
        animation: rotation 1s linear infinite;
    }
    .div_list{
        margin: .24rem;
        background: #fff;
        padding: .24rem;

    }
    .list_top{
        display: flex;
        align-items: center;
        margin-bottom: 0.2rem;
    }
    .shu{
        width: 0.05rem;
        height: 0.3rem;
        margin-right: .2rem;
    }
    .list_title{
        font-size: .34rem;
        font-weight: 600;
    }
    .list_cont{
        display: flex;
        align-items: center;
        margin-top:0.2rem;
    }
    .list_cont_result{
        flex: 1;
        margin-right: 0.1rem;
        text-align: center;
        background: #f8f8f8;
        border-radius: 5px;
        padding: .2rem;
    }
    .list_cont_result p{
        font-size: .26rem;
        color: #909090;
    }
    .posi{
        position:relative;
    }
    .list_org{
        margin-top: .2rem;
        display: flex;
    }
    .list_org .org{
        color: #909090;
        margin-right: .2rem;
    }
    .flex_image{
        flex: 1;
        display: flex;
        align-items: center;
    }
    .flex_image_img{
        width: calc(95% / 3);
        height: 1.8rem;
    }
    .flex_image_img img{
        width: 100%;
        height: 100%;
    }
</style>



